@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yeseva+One&display=swap);
* {
  font-family: 'Oswald', sans-serif;
}

body {
  background-color: #d8bf97;
  margin: 0;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

main {
  padding: 10px 60px;
}
h1 {
  font-size: 35px;
  color: #002d29;
}

h2 {
  font-size: 30px;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card-grid a {
  text-decoration: none;
  color: #002d29;
}
.card {
  align-content: center;
  /* border: 1px solid #80410b;
  border-radius: 6px; */
  margin: 0 10px 10px;
  overflow: hidden;
  max-width: 175px;
}

.card p {
  padding: 5px;
  text-align: center;
  margin: 0;
}

.card img {
  max-width: 170px;
  border-radius: 6px;
}

nav {
  display: flex;
  background-color: #004530;
  padding: 10px;
}
nav a {
  font-size: 20px;
  color: #d3b789;
  padding: 0 10px;
  text-decoration: none;
}
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  flex: 2 1;
}

.logo img {
  max-height: 40px;
}

nav span {
  margin-left: 10px;
  font-family: 'Yeseva One', cursive;
  font-size: 30px;
}
.logo {
  display: flex;
  align-items: center;
}

.drinkContent {
  display: grid;
  grid-template-columns: 45% 2fr;
  grid-template-rows: 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: '. .';
  color: #002d29;
}

figure {
  overflow: hidden;
  max-width: 350px;
  justify-self: end;
  align-self: center;
}

.drinkContent img {
  max-width: 100%;
  border-radius: 6px;
}

.drinkInfo {
  margin: 0 auto;
  width: 75%;
}

.drinkContent section {
  align-self: center;
}

